.top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
  width: 100%;
  background-color: var(--Concrete_Gray);
  color: var(--Light_Gray);
}
.top_left {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  flex: 1;
  padding-left: 10px;
  gap: 15px;
  width: 80%;
}
.top_right {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  padding-right: 10px;
  gap: 15px;
  width: 20%;
}
.top_left_seperator {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  gap: 10px;
}

.nav {
  display: flex;
  flex: 1;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0;
  padding: 20px;
  overflow: hidden;
  position: relative;
}
.nav_left {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 50%;
  gap: 10px;
  overflow: hidden;
}
.nav_right {
  display: flex;
  flex: 1;
  flex-direction: row;
  justify-content: flex-end;
  align-items: start;
  font-weight: bold;
  width: 50%;
  height: 100%;
  position: relative;
}
.hamburger-icon {
  font-size: 50px;
  justify-content: stretch;
}
.me-auto {
  display: flex;
  flex: 1;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: large;
  gap: 20px;
}
.me-auto > * {
  cursor: pointer;
}
.me-auto-zipped {
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 10px;
  padding-right: 15px;
  animation-name: increase !important;
  animation-duration: 5s ease !important;
}

@keyframes increase {
  from {
    display: flex !important;
    height: 0px !important;
  }
  to {
    display: flex !important;
    height: fit-content !important;
  }
}

.nav_seperator {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-left: 1px solid var(--Deep_Red);
  padding: 0 10px;
  cursor: pointer;
}
.sectionone {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  overflow: hidden;
  position: relative;
}
.breadcrumb {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 5px;
}
.slideshow_container {
  position: relative;
  width: 100%;
  height: 700px;
}
.slide_item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 700px;
  overflow: hidden;
}
.prev,
.next {
  user-select: none;
  width: auto;
  cursor: pointer;
  position: absolute;
  border-radius: 50%;
  bottom: 20%;
  transition: 0.6s ease;
  color: rgba(255, 255, 255, 0.484);
}
.prev {
  left: 20px;
}
.next {
  right: 20px;
}

.prev:hover,
.next:hover {
  background-color: rgba(0, 0, 0, 0.235);
  color: rgba(255, 255, 255, 0.92);
  user-select: none;
  border-radius: 50%;
}
.dot_container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  gap: 15px;
  transition: 0.6s ease;
  bottom: 2%;
  position: absolute;
  padding: 20px;
}

.dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 0 2px;
  border-radius: 50%;
  background-color: #ffffff;
  color: #ffffff;
  transition: background-color 0.6s ease;
}
.active,
.dot:hover {
  background-color: #717171;
}
.carusal_text_cont {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  width: 100%;
  height: auto;
}

.fade {
  animation-name: fade;
  animation-duration: 10s;
}
@keyframes fade {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 1;
  }
  50% {
    opacity: 1;
  }
  75% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
@media only screen and (max-width: 300px) {
  .prev,
  .next,
  .text {
    font-size: 11px;
  }
}

.btn {
  padding: 12px;
  border-radius: 5px;
  background-color: transparent;
  transition: background-color 0.3s;
  border: 1px solid #fff;
  cursor: pointer;
}
