.gallery_container {
  padding: 25px;
}
.gallery-header {
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../images/mason01.webp");
  background-size: cover;
  background-position: center;
  padding: 100px 0;
  text-align: center;
  color: white;
}
.section-title {
  position: relative;
  margin-bottom: 40px;
  font-weight: 700;
  color: var(--dark);
  text-align: center;
}

.gallery-content {
  padding: 80px 0;
}
.filter-buttons {
  margin-bottom: 40px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 15px;
}
.filter-btn {
  background-color: white;
  color: var(--dark);
  border: 2px solid var(--Concrete_Gray);
  padding: 10px 25px;
  border-radius: 50px;
  font-weight: 600;
  transition: all 0.3s ease;
  cursor: pointer;
}
.filter-btn:hover,
.filter-btn.active {
  background-color: var(--Concrete_Gray);
  color: white;
}
.gallery-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 25px;
}
.gallery-item {
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  position: relative;
  cursor: pointer;
  height: 250px;
}
.gallery-item:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
}
.gallery-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s ease;
}
.gallery-item:hover .gallery-img {
  transform: scale(1.1);
}
.gallery-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0) 60%);
  opacity: 0;
  transition: opacity 0.3s ease;
  display: flex;
  align-items: flex-end;
  padding: 20px;
  color: white;
}
.gallery-item:hover .gallery-overlay {
  opacity: 1;
}
.gallery-caption {
  transform: translateY(20px);
  transition: transform 0.3s ease;
}
.gallery-item:hover .gallery-caption {
  transform: translateY(0);
}
.modal-content {
  border-radius: 12px;
  overflow: hidden;
}
.modal-img {
  width: 100%;
  border-radius: 8px;
}
.cta-section {
  background-color: var(--dark);
  color: white;
  padding: 80px 0;
  text-align: center;
}
.gallery-item {
  animation: fadeIn 0.5s ease;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@media (max-width: 768px) {
