:root {
  --Brick_Red: #b7410e;
  --Terracotta: #c1440e;
  --Deep_Red: #8b1a1a;
  --Orange: #ff6f00;
  --Muted_Brick: #a23e2d;
  --primary: #8b4513;
  --secondary: #d2691e;
  --accent: #cd853f;
  --Charcoal_Gray: #333333;
  --Dark_Slate: #2c2c2c;
  --dark: #2f4f4f;
  --Sand: #f5f5dc;
  --Beige: #e5d3b3;
  --White: #ffffff;
  --Light_Gray: #f2f2f2;
  --Cream_White: #faf3e0;
  --light: #f5f5dc;
  --Concrete_Gray: #7d7d7d;
  --Gold_Accent: #c9a227;
  --Mustard_Yellow: #ffd447;
  --Steel_Blue: #4682b4;
  --color-black: #000000;
  --color-silver: #c0c0c0;
  --color-dark-gray: #1a1a1a;
  --color-steel-gray: #2c2c2c;
  --color-brick-red: #b22222;
  --color-warm-white: #f5f5f5;
  --color-gold: #d4af37;
  --text-primary: var(--color-warm-white);
  --text-secondary: var(--color-silver);
  --text-accent: var(--color-brick-red);
  --bg-main: var(--color-black);
  --bg-secondary: var(--color-dark-gray);
  --bg-card: var(--color-steel-gray);
  --btn-primary-bg: var(--color-gold);
  --btn-primary-text: var(--color-black);
  --btn-secondary-bg: var(--color-brick-red);
  --btn-secondary-text: var(--color-warm-white);
}

@font-face {
  font-family: "Montserrat Alternates";
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url(https://cdn.jsdelivr.net/fontsource/fonts/montserrat-alternates@latest/latin-400-normal.woff2) format("woff2"), url(https://cdn.jsdelivr.net/fontsource/fonts/montserrat-alternates@latest/latin-400-normal.woff) format("woff"), url(https://cdn.jsdelivr.net/fontsource/fonts/montserrat-alternates@latest/latin-400-normal.ttf) format("truetype");
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+0304, U+0308, U+0329, U+2000-206F, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}
@media (prefers-reduced-motion: no-preference) {
  :root {
    scroll-behavior: smooth;
  }
}

html {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: var(--color-silver);
  color: var(--color-dark-gray);
}

*::-webkit-scrollbar {
  display: none;
}

* {
  scrollbar-width: none;
  -ms-overflow-style: none;
  box-sizing: border-box;
  font-family: "Montserrat Alternates";
}
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

input[type="file"] {
  width: 100%;
  height: 250px;
  padding: 12px;
  border: 2px dashed #3498db;
  border-radius: 5px;
  background-color: #f8f9fa;
}
